const Index = () => {
	const { path } = useParams();
	const [title, setTitle] = useState('');
	const [content, setContent] = useState('');
	const [children, setChildren] = useState([]);
	useEffect(() => {
		console.log('path changed ',path);
		let branch = data;
		if(path!=='index'){
			let pArr = path.split('-');
			for(let i of pArr) {
				if(i)
					branch = branch.children[i];
			}
		}
		setTitle(branch.title);
		setContent(branch.content);
		let chArr = [];
		if(!branch.children){
			setChildren([]);
			return;
		}
		for(let i in branch.children){
			chArr.push({key: i, text: branch.children[i].title})
		}
		setChildren(chArr);
	}, [path])
	return (
		<div className="page">
			<div className="page-main">
			<div className="page-header">
				<div className="page-title">{title}</div>
			</div>
			<div className="page-body">
				{/* {content && (
					<>
						{content.split('\n').map((i,j) => (
							<Fragment key={j}>
								<span dangerouslySetInnerHTML>{i}</span><br />
							</Fragment>
						))}
						<br />
						<br />
					</> */}
					{content && <div dangerouslySetInnerHTML={{__html:content.replace(/\n/g, '<br />')}} />}
				{/* )} */}
				<ol>
					{children.map((i,j) => (
						<li key={j}><Link to={"/"+(path==='index'?'':path)+"-"+i.key} key={j}>{i.text}</Link></li>
					))}
				</ol>
			</div>
			</div>
		</div>
	)
}
