import './App.css'
import { Route, BrowserRouter as Router, Switch } from 'react-router-dom'
import Home from './pages/Home'
import Index from './pages/Index'

const App = () => {
	return (
		<Router>
			<Switch>
				<Route path="/:path" children={<Index />} />
				<Route path="/" children={<Home />} />
			</Switch>
		</Router>
	)
}

export default App